module.exports = "class Example extends React.Component {\n  constructor(...args) {\n    super(...args);\n    this.state = {\n      open: true\n    };\n  }\n\n  render() {\n    return (\n      <div>\n        <Button onClick={() => this.setState({ open: !this.state.open })}>\n          Click to toggle\n        </Button>\n        <br />\n        <Panel id=\"collapsible-panel-example-1\" expanded={this.state.open}>\n          <Panel.Collapse>\n            <Panel.Body>\n              Anim pariatur cliche reprehenderit, enim eiusmod high life\n              accusamus terry richardson ad squid. Nihil anim keffiyeh\n              helvetica, craft beer labore wes anderson cred nesciunt sapiente\n              ea proident.\n            </Panel.Body>\n          </Panel.Collapse>\n        </Panel>\n\n        <p>You can also make the Panel heading toggle the collapse.</p>\n\n        <Panel id=\"collapsible-panel-example-2\" defaultExpanded>\n          <Panel.Heading>\n            <Panel.Title toggle>\n              Title that functions as a collapse toggle\n            </Panel.Title>\n          </Panel.Heading>\n          <Panel.Collapse>\n            <Panel.Body>\n              Anim pariatur cliche reprehenderit, enim eiusmod high life\n              accusamus terry richardson ad squid. Nihil anim keffiyeh\n              helvetica, craft beer labore wes anderson cred nesciunt sapiente\n              ea proident.\n            </Panel.Body>\n          </Panel.Collapse>\n        </Panel>\n\n        <p>Or use a Panel.Toggle component to customize</p>\n\n        <Panel id=\"collapsible-panel-example-3\" defaultExpanded>\n          <Panel.Heading>\n            <Panel.Title>Title that functions as a collapse toggle</Panel.Title>\n            <Panel.Toggle componentClass=\"a\">My own toggle</Panel.Toggle>\n          </Panel.Heading>\n          <Panel.Collapse>\n            <Panel.Body>\n              Anim pariatur cliche reprehenderit, enim eiusmod high life\n              accusamus terry richardson ad squid. Nihil anim keffiyeh\n              helvetica, craft beer labore wes anderson cred nesciunt sapiente\n              ea proident.\n            </Panel.Body>\n          </Panel.Collapse>\n        </Panel>\n      </div>\n    );\n  }\n}\n\nrender(<Example />);\n"


