module.exports = "class ControlledPanelGroup extends React.Component {\n  constructor(...args) {\n    super(...args);\n\n    this.handleSelect = this.handleSelect.bind(this);\n\n    this.state = {\n      activeKey: '1'\n    };\n  }\n\n  handleSelect(activeKey) {\n    this.setState({ activeKey });\n  }\n\n  render() {\n    return (\n      <PanelGroup\n        accordion\n        id=\"accordion-controlled-example\"\n        activeKey={this.state.activeKey}\n        onSelect={this.handleSelect}\n      >\n        <Panel eventKey=\"1\">\n          <Panel.Heading>\n            <Panel.Title toggle>Panel heading 1</Panel.Title>\n          </Panel.Heading>\n          <Panel.Body collapsible>Panel content 1</Panel.Body>\n        </Panel>\n        <Panel eventKey=\"2\">\n          <Panel.Heading>\n            <Panel.Title toggle>Panel heading 2</Panel.Title>\n          </Panel.Heading>\n          <Panel.Body collapsible>Panel content 2</Panel.Body>\n        </Panel>\n      </PanelGroup>\n    );\n  }\n}\n\nrender(<ControlledPanelGroup />);\n"


